import React, {Component} from 'react';
import { bindActionCreators } from 'redux';
import {Lifecycle, RouteContext} from 'react-router';
import { connect } from 'react-redux';

import * as IndexActions from 'ACTION/index';

import dataService from 'DATASERVICE';
import Util from 'UTIL';
import Auth from './auth';

const Api = {
  basic: '/passerbyking/game/info/:id'
};


class Layout extends Component {
  constructor(props) {
    super();

    this.state = {
      game_id: Util.getGameId(),
      title: ''
    }
  }
  componentWillMount() {
    const { actions } = this.props;

    let url = Api.basic.replace(':id', this.state.game_id);

    dataService
      .get(url)
      .then(result => {
        if(result.code == 1){
          actions.addBasic(result.data);

          this.setState(result.data);
        }
      });
  }
  componentWillReceiveProps(nextProps) {
    const {basicInfo} = nextProps;

    // 
    Auth(basicInfo.match_status, nextProps.location);
  }
  render() {
    return (    
      <div>
        {this.props.children}
      </div> 
    );
  }
};

const mapStateToProps = state => {
  return {
    basicInfo: state.basicInfo
  }
}

const mapDispatchToProps = dispatch => {
  return {
    actions: bindActionCreators(IndexActions, dispatch)
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Layout);




