import React from 'react';
import Modal, {Toast} from 'MODAL';
import dataService from 'DATASERVICE';
import Util from 'UTIL';

import './list.scss';

const Api = {
    giveup: '/passerbyking/player/giveup'
};

class List extends React.Component{	
	constructor(props) {
    super(props);

    this.state = {

    }
  }
  componentWillMount() {
  }
	handleWaiver(player_puid = 0, nickname = ''){
		let styles = {
			color: "#c01e2f"
		}
		let contentElement = (
			<div style={styles}></div>
		)

		Modal.alert(`${nickname} `, contentElement, () => {
      let params = {
        game_id: this.props.game_id,
        player_puid
      }

	    dataService
	      .post(Api.giveup, params)
	      .then(result => {
          Toast.show(result.msg);

          if(result.code == 1){
          	this.props.callback && this.props.callback();
          }
	      })
    });
	}
	getListElement(item, index) {
		let number = (index + 1);

		let status = (+item.status);
		let gameClassName = (status !== 1 && this.props.isOptionButton) ? 'ui-play-list-gave' : '';
		let nickname = Util.subBtyesString(item.nickname);

		const getElement = (
				<div key={index} className={`ui-play-list ${gameClassName} ui-play-list-number-${number}`}>
					<div className="list-left">{item.player_num}</div>
					<div className="list-mid">
						<div className="avatar">
							<img src={item.header} />
						</div>
						<div className="list-text">
							<span className="player-nick">{nickname}</span>
							<div className="play-message">{item.height}cm | {item.weight}kg | {item.total_win}({item.rate}%)</div>
						</div>
					</div>
					{
						this.props.isTopNumber ? (
							<div className="list-rank">
								<span className="rank-item">{index + 1}</span>
							</div>
						) : null
					}
					{
						((status != 2) && this.props.isOptionButton) ? (
							<div className="list-right">
								{(() => {
									switch (status) {
					          case 0:     return <span className="waiver"></span>;
					          case 3:     return <span className="waiver"></span>;
					          default:    return <span className="waiver" onClick={this.handleWaiver.bind(this, item.puid, nickname)}></span>;
					        }
								})()}
							</div>
						) : null
					}
				</div>
			)


		return getElement;
	}
	render(){
		let lists = this.props.lists;

		return (
			<div>
				{
					lists.map((item, index) => {
						return this.getListElement(item, index)
					})
				}
			</div>
		);
	}
}

List.defaultProps = {
	game_id: '',
	lists: [],
	callback: () => {},
	isTopNumber: false,
	isOptionButton: true
}

List.propTypes = {
	// game_id: React.PropTypes.number,
	lists: React.PropTypes.array,
	callback: React.PropTypes.func,
	isOptionButton: React.PropTypes.bool,
	isTopNumber: React.PropTypes.bool
}

export default List;


