import React, {Component} from 'react';
import {Link} from 'react-router';
import Modal, {Popup} from 'MODAL';
import Util from 'UTIL';

import "./list.scss";

class List extends Component {
  constructor(props) {
    super();

    this.state = {
      option_area: {},
      visibleModal: false   
    }
  }
  componentWillMount() {
    // this.setProps();
  }
  componentWillReceiveProps(nextProps) {
    // this.setProps(nextProps);
  }
  
  getListElement(item, index) {
    const number = (index + 1);

    const {left, right} = item;
    const matchText = {
      5: {
        left: '1',
        right: '2'
      },
      6: {
        left: '3',
        right: '4'
      },
      7: {
        left: '1',
        right: '2'
      },
      8: {
        left: '3',
        right: '4'
      },
      9: {
        left: '7',
        right: '6'
      },
      10: {
        left: '8',
        right: '5'
      },
      11: {
        left: '7',
        right: '8'
      },
      12: {
        left: '9',
        right: '10'
      },
      13: {
        left: '11',
        right: '12'
      },
      14: {
        left: '11 ',
        right: '13'
      }
    }


    let isFail = (type) => {
      let className = '';

      if(type === 'left') {
          className = (item.status == 2 ? 'match-fail' : '');
        }else{
          className = (item.status == 1 ? 'match-fail' : '');
        }

      return className;
    }

    const getElement = (
      <div key={index} className={`semifinal-item semifinal-item-notstart`}>
        {
          (this.props.isTitle && item.num >= 11 && item.num < 14) ? (
              <div className="match-title">
                <h5></h5>
              </div>
          ) : null
        }
        {
          (this.props.isTitle && item.num == 14) ? (
              <div className="match-title">
                <h5></h5>
              </div>
          ) : null
        }
        <div className="match-row">
          <div className="col-left">
            {
              left ? (
                <div className={`${isFail("left")}`}>
                  <div className="avatar-inner">
                    <span className="number">{left.player_num}</span>
                    <div className="avatar">
                      <img src={left.header} />
                    </div>
                  </div>
                  <h4 className="title">{Util.subBtyesString(left.nickname)}</h4>
                  <div className="info">
                    {left.total_win}({left.rate}%)
                  </div>
                </div>
              ) : (
                <span className="unknown-person">{matchText[item.num].left}</span>
              )
            }
          </div>
          <div className="col-center">
            <div className="match-score">
              <div className="course-number">{item.num}</div>
              <div className="score">
                {
                  !item.status ? 
                    "" : (
                      <div>{item.left_score}:{item.right_score}</div>
                    )
                }
              </div>
            </div>
          </div>
          <div className="col-right">
            {
              right ? (
                <div className={`${isFail("right")}`}>
                  <div className="avatar-inner">
                    <div className="avatar">
                      <img src={right.header} />
                    </div>
                    <span className="number">{right.player_num}</span>
                  </div>

                  <h4 className="title">{Util.subBtyesString(right.nickname)}</h4>
                  <div className="info">
                    {right.total_win}({right.rate}%)
                  </div>
                </div>
              ) : (
                <span className="unknown-person">{matchText[item.num].right}</span>
              )
            }
          </div>
        </div>
      </div>
    )

    return getElement;
  }
  render() {
    let {lists} = this.props;

    return (
      <div className="ui-semifinal-list">
        {
          lists.map((item, index) => {
            if(item.right_puid || item.left_puid){
              return this.getListElement(item, index)
            }
          })
        }
      </div>  
    );
  }
};

List.defaultProps = {
  lists: [],
  isTitle: false
}

List.propTypes = {
  lists: React.PropTypes.array,
  isTitle: React.PropTypes.bool
}

export default List;




